// frontend/app/page.tsx
'use client'; // Ensure client-side rendering

import React, { useState, useEffect } from 'react';
import toast, { Toaster } from 'react-hot-toast';
import Header from '../components/Header';
import Footer from '../components/Footer';
import DynamicSearch from '../components/DynamicSearch';
import SlidingTemplates from '../components/SlidingTemplates'; // Assuming this exists
import { useWalletStore } from '../stores/walletStore';
import { Server } from '@stellar/stellar-sdk/rpc'; // Kept for consistency

const Home = () => {
  const [templates, setTemplates] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { wallet } = useWalletStore();

  useEffect(() => {
    const fetchTemplates = async () => {
      if (!wallet?.address) {
        setError('Wallet not connected');
        setIsLoading(false);
        toast.error('Wallet not connected. Please connect your wallet.');
        return;
      }

      setIsLoading(true);
      setError(null);
      try {
        const response = await fetch('/api/templates');
        if (!response.ok) throw new Error('Failed to fetch templates');
        const data = await response.json();
        setTemplates(data);
      } catch (err) {
        setError(err.message || 'Failed to fetch templates');
        toast.error(`Error fetching templates: ${err.message || 'Unknown error'}`);
      } finally {
        setIsLoading(false);
      }
    };

    fetchTemplates();
  }, [wallet]);

  // Dynamically import useRouter for navigation
  

  if (isLoading) return <div className="text-center text-gray-600">Loading templates...</div>;
  if (error) return <div className="text-red-500 text-center">{error}</div>;

  return (
    <div className="min-h-screen flex flex-col">
      <Toaster position="top-right" />
      <Header />
      <main className="flex-grow">
        <div className="container mx-auto p-4">
          <h1 className="text-3xl font-bold mb-6 text-center">Lending Pool Marketplace</h1>
          {templates.length > 0 && <SlidingTemplates templates={templates} />}
          <DynamicSearch />
          <div className="mt-6">
            <h2 className="text-2xl font-semibold mb-4 text-center">Template List</h2>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
              {templates.map((template) => (
                <div
                  key={template.id}
                  className="border rounded-lg shadow-lg p-4 cursor-pointer hover:bg-gray-100"
                  onClick={() => handleNavigate(`/templates/${template.template_id}`)}
                >
                  <h3 className="text-lg font-bold">{template.template_id}</h3>
                  <p>APY: {(template.parameters.apy / 100).toFixed(2)}%</p>
                  <p>TVL: {template.parameters.tvl.toLocaleString()} XLM</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </main>
      <Footer />
      <button
        onClick={() => handleNavigate('/deploy')}
        className="fixed bottom-6 right-6 bg-green-500 text-white p-4 rounded-full shadow-lg hover:bg-green-600 transition-colors"
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
        </svg>
      </button>
    </div>
  );
};

export default Home;
